import AppDispatcher from 'app/dispatcher/AppDispatcher'

class ButtonActions {

    //ADD_NEW_ITEMAction
    addNewItem(text) {
        //DispatcheractionTypeADD_NEW_ITEMAction
        AppDispatcher.dispatch({
            actionType: 'ADD_NEW_ITEM',
            text: text
        })
    }
    removeNewItem(id) {
        AppDispatcher.dispatch({
            actionType: 'REMOVE_NEW_ITEM',
            id: id
        })
    }
}

export default ButtonActions


