import React, {Component} from 'react'
import MyButton from 'app/components/MyButton'
import listStore from 'app/stores/listStore'
import ButtonActions from 'app/actions/ButtonActions'

//ActionbuttonActionsactionDispatcher
let buttonActions = new ButtonActions()

class MyButtonController extends Component {

    constructor(props) {
        //propssuper(),constructorthis.props
        super(props)
        this.state = {
            items: [],
            value:"",
            key:""
        }
    }

    componentDidMount() {
        //_onChangeStore,listStore_onChange
        listStore.addChangeListener(this._onChange.bind(this))
    }

    componentWillUnmount() {
        //_onChangeStore
        listStore.removeChangeListener(this._onChange.bind(this))
    }

    //Store
    _onChange() {
        this.setState({
            items: listStore.getAll()
        })
    }
    getId(id) {
        this.setState({
            key:id
        })
    }

    render() {
        return (
            <MyButton
                items={this.state.items}
                onClick={this.createNewItem.bind(this)}
                reMove={this.delNewItem}
                value={this.state.value}
                changeValue={this.changeValue.bind(this)}
            />
        )
    }
    createNewItem() {
        //ActionItemAction
        buttonActions.addNewItem(this.state.value)
    }
    delNewItem(key) {
        console.log(this)
        buttonActions.removeNewItem(key)
    }
    changeValue(event) {
        this.setState({
            value: event.target.value
        })
    }
}

export default MyButtonController


